*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar{
  z-index: 5;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 100px;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar a{
  text-transform: uppercase;
}
.navbar a:hover{
  color: #3498db;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
.brand-name{
  color: #3498db;
  margin-bottom: 30px;
}
#header .animated{
  animation:  up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}




@media (max-width: 991px) {
  .menu_active{
    border-bottom: none;
  }
  #header{
    height: auto;
  }
}



